.footer{
  &__nav {
    grid-row: 5 / 6;
    grid-column: 1 / 3;
    font-family: comic_strip;
    color: white;
    display: flex;
    height: 60px;
    align-items: center;
  }
  &__text-1 {
    padding: 0 0 0 20px;
  }
  &__link {
    padding: 0 20px;
    &--milda {
      font-size: 22px;
    }
    &--milda:hover {
      animation: text-flicker-out-glow 2.5s linear both;
      font-size: 26px;
    }
    &--adalab {
      font-size: 22px;
    }
    &--adalab:hover {
      color: rgb(24,217,196);
      font-size: 26px;
    }
  }
}